
import React, { useRef, useEffect, useState } from "react";
import { Container, Row, Col, Card, CardBody } from "reactstrap";
import LogoAlInvest from "../assets/images/logos-importantes/Al-Invest-verde.jpg";
import LogoGlobal from "../assets/images/logos-importantes/Global.jpg";
import LogoMundoVerde from "../assets/images/logos-importantes/mundo_verde.jpg";
import LogoSequa from "../assets/images/logos-importantes/Sequa.jpg";
import LogoUnionEuropea from "../assets/images/logos-importantes/Uniion_europea.jpg";

const contribuyentes = [
  { src: LogoGlobal, alt: "Global" },
  { src: LogoAlInvest, alt: "Al-Invest Verde" },
  { src: LogoUnionEuropea, alt: "Unión Europea" },
  { src: LogoSequa, alt: "Sequa" },
  { src: LogoMundoVerde, alt: "Mundo Verde" },
];

const Contribuyentes = () => {
  const scrollRef = useRef(null);
  const [isDragging, setIsDragging] = useState(false);
  const [startX, setStartX] = useState(0);
  const [scrollLeft, setScrollLeft] = useState(0);
  const [isMobileOrTablet, setIsMobileOrTablet] = useState(() => window.innerWidth <= 991);

  // Detectar si es móvil/tablet y actualizar en resize
  useEffect(() => {
    const handleResize = () => {
      setIsMobileOrTablet(window.innerWidth <= 991);
    };
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Auto-scroll y drag solo en móvil/tablet
  useEffect(() => {
    if (!isMobileOrTablet) return;
    const scrollContainer = scrollRef.current;
    let scrollAmount = 0;
    let direction = 1;
    let animationFrame;

    function autoScroll() {
      if (!scrollContainer || isDragging) {
        animationFrame = requestAnimationFrame(autoScroll);
        return;
      }
      scrollAmount = scrollContainer.scrollLeft + direction * 2.5;
      if (scrollAmount >= scrollContainer.scrollWidth - scrollContainer.clientWidth) {
        direction = -1;
      } else if (scrollAmount <= 0) {
        direction = 1;
      }
      scrollContainer.scrollTo({ left: scrollAmount, behavior: 'smooth' });
      animationFrame = requestAnimationFrame(autoScroll);
    }
    animationFrame = requestAnimationFrame(autoScroll);

    // Drag events solo en móvil/tablet
    const handleMouseDown = (e) => {
      setIsDragging(true);
      setStartX(e.pageX - scrollContainer.offsetLeft);
      setScrollLeft(scrollContainer.scrollLeft);
      scrollContainer.style.cursor = 'grabbing';
    };
    const handleMouseLeave = () => {
      setIsDragging(false);
      scrollContainer.style.cursor = 'grab';
    };
    const handleMouseUp = () => {
      setIsDragging(false);
      scrollContainer.style.cursor = 'grab';
    };
    const handleMouseMove = (e) => {
      if (!isDragging) return;
      e.preventDefault();
      const x = e.pageX - scrollContainer.offsetLeft;
      const walk = (x - startX) * 1.5;
      scrollContainer.scrollLeft = scrollLeft - walk;
    };

    scrollContainer.addEventListener('mousedown', handleMouseDown);
    scrollContainer.addEventListener('mouseleave', handleMouseLeave);
    scrollContainer.addEventListener('mouseup', handleMouseUp);
    scrollContainer.addEventListener('mousemove', handleMouseMove);
    scrollContainer.style.cursor = 'grab';

    // Touch events para móvil/tablet
    let touchStartX = 0;
    let touchScrollLeft = 0;
    const handleTouchStart = (e) => {
      setIsDragging(true);
      touchStartX = e.touches[0].pageX;
      touchScrollLeft = scrollContainer.scrollLeft;
    };
    const handleTouchEnd = () => setIsDragging(false);
    const handleTouchMove = (e) => {
      if (!isDragging) return;
      const x = e.touches[0].pageX;
      const walk = (x - touchStartX) * 1.5;
      scrollContainer.scrollLeft = touchScrollLeft - walk;
    };
    scrollContainer.addEventListener('touchstart', handleTouchStart);
    scrollContainer.addEventListener('touchend', handleTouchEnd);
    scrollContainer.addEventListener('touchmove', handleTouchMove);

    return () => {
      cancelAnimationFrame(animationFrame);
      scrollContainer.removeEventListener('mousedown', handleMouseDown);
      scrollContainer.removeEventListener('mouseleave', handleMouseLeave);
      scrollContainer.removeEventListener('mouseup', handleMouseUp);
      scrollContainer.removeEventListener('mousemove', handleMouseMove);
      scrollContainer.removeEventListener('touchstart', handleTouchStart);
      scrollContainer.removeEventListener('touchend', handleTouchEnd);
      scrollContainer.removeEventListener('touchmove', handleTouchMove);
    };
  }, [isDragging, isMobileOrTablet, startX, scrollLeft]);

  return (
    <section className="section bg-white" id="contribuyentes">
      <Container>
        <Row className="justify-content-center mb-4">
          <Col lg={8} className="text-center">
            <h2 className="fw-bold mb-4" style={{ letterSpacing: 1, color: '#217a3a', fontSize: '2.5rem', textShadow: '0 2px 12px #e0f2f1' }}>Contribuyentes</h2>
          </Col>
        </Row>
        <Row>
          <Col xs={12}>
            <Card className="shadow border-0 rounded-4 p-3 w-100" style={{ background: 'linear-gradient(90deg, #e8f5e9 0%, #f8fafc 100%)' }}>
              <CardBody className="d-flex flex-column align-items-center p-0">
                <div
                  ref={scrollRef}
                  className="contribuyentes-carousel"
                  style={{
                    width: '100%',
                    minHeight: 180,
                    height: 180,
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                    border: '2.5px solid #b2dfdb',
                    borderRadius: 18,
                    background: '#fff',
                    padding: '0 32px',
                    gap: 48,
                    boxShadow: '0 8px 32px rgba(33, 122, 58, 0.07)',
                    flexWrap: 'nowrap',
                    justifyContent: 'center',
                    overflow: 'hidden',
                  }}
                >
                  {contribuyentes.map((logo, idx) => (
                    <div key={idx} style={{display: 'flex', alignItems: 'center', justifyContent: 'center', minWidth: 180, minHeight: 120}}>
                      <img
                        src={logo.src}
                        alt={logo.alt}
                        style={{
                          height: 110,
                          maxWidth: 180,
                          objectFit: 'contain',
                          filter: 'drop-shadow(0 2px 8px #b2dfdb)',
                          background: 'white',
                          borderRadius: 12,
                          padding: 12,
                          transition: 'transform 0.3s',
                        }}
                        className="contribuyente-logo"
                      />
                    </div>
                  ))}
                </div>
                <style>{`
                  .contribuyentes-carousel::-webkit-scrollbar { display: none; }
                  .contribuyente-logo:hover { transform: scale(1.08); box-shadow: 0 4px 24px #a5d6a7; }
                  @media (max-width: 991px) {
                    .contribuyentes-carousel {
                      gap: 24px;
                      padding: 0 12px;
                      min-height: 140px;
                      height: 140px;
                      overflow-x: auto;
                      -webkit-overflow-scrolling: touch;
                      cursor: grab;
                      flex-wrap: nowrap;
                      justify-content: flex-start;
                      overflow: visible;
                    }
                    .contribuyente-logo {
                      height: 80px;
                      max-width: 120px;
                      padding: 8px;
                    }
                  }
                `}</style>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default Contribuyentes;
